<template>
  <div>
    <div id="map"></div>
    <div class="button-group">
      <base-button @click="displayMarker(markerPositions1)">둘러보기</base-button>
      <base-button @click="displayMarker([])">마커 지우기</base-button>
    </div>
    <BottomSheet v-model="isBottomSheetOpen" :place="selectedPlace" />
  </div>
</template>

<script>
import { toRaw } from "vue";
import BottomSheet from "./BottomSheet.vue";
import BaseButton from "./BaseButton.vue";

export default {
  components: { BaseButton, BottomSheet },
  name: "KakaoMap",
  data() {
    return {
      markerPositions1: [
        {
          position: [36.325115845255, 127.33739274904492],
          title: "로봇학과 주점",
          description: "주점 ~~~~입니다.",
          image: "/images/qwer.webp",
        },
        {
          position: [36.326115845255, 127.33839274904492],
          title: "두 번째 장소",
          description: "두 번째 장소에 대한 상세 설명입니다.",
          image: "https://example.com/second-place-image.jpg",
        },
      ],
      markers: [],
      clickedCoords: null,
      isBottomSheetOpen: false,
      selectedPlace: null,
      map: null,
    };
  },

  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src = "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=915cffed372954b7b44804ed422b9cf0";
      document.head.appendChild(script);
    }
  },

  methods: {
    initMap() {
      const container = document.getElementById("map");
      const options = {
        center: new kakao.maps.LatLng(36.325720567594026, 127.33865633510858),
        level: 4,
      };

      this.map = new kakao.maps.Map(container, options);

      // 지도 클릭 시 바텀 시트 닫기
      kakao.maps.event.addListener(this.map, "click", (mouseEvent) => {
        const latlng = mouseEvent.latLng;
        this.clickedCoords = { lat: latlng.getLat(), lng: latlng.getLng() };
        this.isBottomSheetOpen = false;
      });
    },

    displayMarker(markerPositions) {
      // 기존 마커가 있다면 지우기
      if (this.markers.length > 0) {
        this.markers.forEach((marker) => marker.setMap(null));
      }

      if (markerPositions.length > 0) {
        this.markers = markerPositions.map((item, index) => {
          const marker = new kakao.maps.Marker({
            map: toRaw(this.map),
            position: new kakao.maps.LatLng(...item.position),
            title: item.title,
          });

          // 마커 클릭 이벤트
          kakao.maps.event.addListener(marker, "click", () => {
            this.selectedPlace = item; // 클릭한 마커의 정보 저장
            this.isBottomSheetOpen = true; // 바텀 시트 열기
          });

          return marker;
        });

        // 모든 마커가 보이도록 지도 범위 재설정
        const bounds = new kakao.maps.LatLngBounds();
        this.markers.forEach((marker) => {
          bounds.extend(marker.getPosition());
        });
        toRaw(this.map).setBounds(bounds);
        toRaw(this.map).setLevel(4);
      }
    },
  },
};
</script>

<style scoped>
#map {
  width: 100%;
  height: 400px;
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 1.7rem;
  margin-block: 2rem;
}
</style>
