<!-- BottomSheet.vue -->
<template>
  <Transition name="slide">
    <div v-if="modelValue" class="bottom-sheet">
      <div class="bottom-sheet-overlay" @click="closeSheet"></div>
      <div class="bottom-sheet-content">
        <div class="handle-bar"></div>
        <div v-if="place" class="place-info">
          <img v-if="place.image" :src="place.image" :alt="place.title" class="place-image" />
          <div class="place-details">
            <h3>{{ place.title }}</h3>
            <p>{{ place.description }}</p>
          </div>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  name: "BottomSheet",
  props: {
    modelValue: Boolean,
    place: Object,
  },
  emits: ["update:modelValue"],
  methods: {
    closeSheet() {
      this.$emit("update:modelValue", false);
    },
  },
};
</script>

